/* purgecss start ignore */

nav{
  z-index: 20;
  position: fixed;
  width: 100%;
  max-height: 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}

.nav-1{
  background-color: #05374200;
  transition: all .5s;
}

.nav-2{
  background: #053742;
  transition: all .5s;
}

.navbar-title{
  z-index: 10;
  font-size: 2rem;
  font-weight: 600;
  margin: 1rem;
  /* display: none; */
}

.nav-1 .title-meanMech{
  display: none;
  color: #ffffff00;
  text-decoration: none;
}

.nav-2 .title-meanMech{
  color: #ffffff;
  text-decoration: none;
}

.navbar-links ul{
  margin: 0;
  padding: 0;
  font-size: 1.3rem;
  display: flex;
}

.navbar-links ul li{
  list-style: none;
}

.navbar-links li a{
  text-decoration: none;
  color: #ffffffbd;
  margin: 1rem;
  display: block;
}

.navbar-links .active-navlink{
  border-bottom: 1px solid white;
  color: white;
  transition: all 0.2s;
}

.navbar-links a:hover{
  color: #ffffff;
}

#check{
  display: none;
}

.nav-checkbtn{
  position: absolute;
  top: 1.3rem;
  right: 1.3rem;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 25px;
}

.nav-checkbtn .bar{
  height: 3px;
  width: 100%;
  background-color: white;
  border-radius: 10px;
}

@media (max-width: 900px){
  .navbar-title{
    font-size: 1.9rem;
  }

  .nav-checkbtn{
    display: flex;
  }

  .nav-1{
    flex-direction: column;
    align-items: flex-start;
  }

  .navbar-links ul{
    z-index: 9;
    top: 0;
    padding-top: 4rem;
    background: #053742cb;
    flex-direction: column;
    width: 100%;
    height: 100%;
    position: fixed;
    text-align: right;
    right: 100%;
    transition: all 0.5s;
  }

  .navbar-links ul li{
    display: block;
    line-height: 1rem;
  }

  .navbar-links ul li a{
    padding: 1rem 2rem;
    margin: 0;
  }

  .navbar-links .active-navlink{
    border: none;
    color: #053742;
    background: #ffffffe7;
    transition: all .2s;
  }

  .navbar-links a:hover{
    color: #053742;
    background: #ffffffbd;
    transition: 0.5s;
  }

  #check:checked ~ .navbar-links ul{
    right: 72%;
  }
}

@media (max-width: 600px){
  #check:checked ~ .navbar-links ul{
    right: 61%;
  }

  .navbar-title{
    font-size: 1.8rem;
  }
}

@media (max-width: 450px){
  #check:checked ~ .navbar-links ul{
    right: 50%;
  }

  .navbar-title{
    font-size: 1.7rem;
  }
}

